@import url("https://fonts.googleapis.com/css?family=Amatic+SC");
/* font-family: 'Amatic SC', cursive; */
@import url("https://fonts.googleapis.com/css?family=Seaweed+Script");
/* font-family: 'Seaweed Script', cursive; */
@import url("https://fonts.googleapis.com/css?family=Leckerli+One");
/* font-family: 'Leckerli One', cursive; */
@import url("https://fonts.googleapis.com/css?family=Playfair+Display");
/* font-family: 'Playfair Display', serif; */
@import url("https://fonts.googleapis.com/css?family=Roboto+Slab");
/* font-family: 'Roboto Slab', serif; */
@import url("https://fonts.googleapis.com/css?family=Vesper+Libre");
@import url("https://fonts.googleapis.com/css?family=Open+Sans");
/* font-family: 'Open Sans', sans-serif; */
html {
  box-sizing: border-box;
  font-size: 10px; }

html,
body,
#root {
  width: 100%;
  height: 100%;
  overflow: hidden; }

* {
  margin: 0;
  padding: 0; }

ul {
  list-style: none; }

a {
  text-decoration: none;
  color: #393939; }

*,
*:before,
*:after {
  box-sizing: inherit;
  -webkit-overflow-scrolling: touch;
  outline: 0;
  border: 0; }

body {
  margin: 0;
  padding: 0;
  font-size: 1.6rem;
  line-height: 2;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  /* overflow-x: hidden;
  overflow-y: scroll; */ }

button,
button:focus a,
a:focus,
a:link,
a:active,
a:visited {
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  background-color: rgba(0, 0, 0, 0); }

p {
  font-size: 16px;
  line-height: 1.5; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace; }

.menu-container {
  background: #fff; }
  .menu-container .menuContentBox {
    margin: 10px auto; }
    @media (max-width: 359px) {
      .menu-container .menuContentBox {
        margin: 5px auto; } }
    .menu-container .menuContentBox h1 {
      /* ${props => props.theme.fontLeckerli} */
      color: #232d3e;
      text-align: center;
      font-family: "Amatic SC", cursive;
      font-size: 64px;
      font-weight: 700;
      line-height: 1.4; }
      @media (max-width: 767px) {
        .menu-container .menuContentBox h1 {
          font-size: 44px; } }
    .menu-container .menuContentBox .itemsBox {
      margin-top: 10px;
      padding: 20px;
      /* background: rgba(0, 0, 0, 0.5); */ }
      @media (max-width: 767px) {
        .menu-container .menuContentBox .itemsBox {
          padding: 10px; } }
      @media (max-width: 359px) {
        .menu-container .menuContentBox .itemsBox {
          padding: 4px; } }
      .menu-container .menuContentBox .itemsBox .itemBoxGrid {
        display: grid;
        grid-gap: 20px;
        grid-template-columns: 1fr 1fr;
        /* grid-template-columns: repeat(auto-fit, minmax(400px, 1fr)); */ }
        @media (max-width: 767px) {
          .menu-container .menuContentBox .itemsBox .itemBoxGrid {
            grid-gap: 10px;
            grid-template-columns: 1fr; } }
        .menu-container .menuContentBox .itemsBox .itemBoxGrid .itemBox {
          /* background: rgba(255, 0, 0, 0.5); */
          display: flex;
          justify-content: center; }
          .menu-container .menuContentBox .itemsBox .itemBoxGrid .itemBox .imageBox {
            overflow: hidden;
            width: 160px;
            height: 160px;
            border-radius: 80px;
            /* width: 200px;
            height: 140px;
            border-radius: 8px; */
            /* position: relative; */ }
            @media (max-width: 1023px) {
              .menu-container .menuContentBox .itemsBox .itemBoxGrid .itemBox .imageBox {
                width: 140px;
                height: 140px;
                border-radius: 70px; } }
            .menu-container .menuContentBox .itemsBox .itemBoxGrid .itemBox .imageBox img {
              /* position: absolute;
              left: 50%;
              transform: translateX(-50%); */
              -webkit-backface-visibility: hidden;
              backface-visibility: hidden;
              width: 100%;
              height: 100%;
              user-select: none;
              filter: brightness(110%); }
          .menu-container .menuContentBox .itemsBox .itemBoxGrid .itemBox .contentBox {
            flex: 1;
            margin-left: 20px;
            display: flex;
            flex-direction: column;
            justify-content: center; }
            @media (max-width: 767px) {
              .menu-container .menuContentBox .itemsBox .itemBoxGrid .itemBox .contentBox {
                margin-left: 10px; } }
            .menu-container .menuContentBox .itemsBox .itemBoxGrid .itemBox .contentBox .name {
              position: relative;
              font-family: "Roboto Slab", serif;
              color: #393939;
              font-weight: bold;
              font-size: 18px;
              line-height: 1.5; }
              .menu-container .menuContentBox .itemsBox .itemBoxGrid .itemBox .contentBox .name .nameDescription {
                position: absolute;
                font-size: 10px;
                top: 30%;
                margin-left: 4px;
                color: #35455f; }
                @media (max-width: 359px) {
                  .menu-container .menuContentBox .itemsBox .itemBoxGrid .itemBox .contentBox .name .nameDescription {
                    top: 35%;
                    font-size: 9px;
                    margin-left: 2px; } }
              .menu-container .menuContentBox .itemsBox .itemBoxGrid .itemBox .contentBox .name .day {
                position: absolute;
                font-size: 12px;
                font-style: italic;
                margin-top: -18px;
                top: 0;
                left: 10%;
                padding: 1px 6px;
                border-radius: 9px;
                background: #008b00;
                color: #ededed; }
            .menu-container .menuContentBox .itemsBox .itemBoxGrid .itemBox .contentBox .price {
              font-size: 18px;
              font-weight: bold;
              font-family: "Playfair Display", serif;
              color: #a50000; }
              .menu-container .menuContentBox .itemsBox .itemBoxGrid .itemBox .contentBox .price .striked {
                font-family: "Roboto Slab", serif;
                color: #8b0000;
                text-decoration: line-through; }
              .menu-container .menuContentBox .itemsBox .itemBoxGrid .itemBox .contentBox .price .dollar-sign {
                font-family: "Roboto Slab", serif;
                margin-right: 2px; }
            .menu-container .menuContentBox .itemsBox .itemBoxGrid .itemBox .contentBox .description {
              color: #3a3a3a;
              font-style: italic;
              line-height: 1.2; }
