@import './global';

.info-box {
  margin-top: 10px;
  margin-bottom: 10px;
  width: 100%;
  background: url('/images/menu-outer-bg3.jpg');
  background-repeat: repeat;
  // padding: 1rem 0 1rem 0;
  // padding: 20px;
  padding: 10px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  // flex-direction: column;
  h2 {
    line-height: 1.3;
    color: $offWhite;
    background: $darkNavy;
    display: inline-block;
    // height: 60px;
    padding: 10px 15px;
    border-radius: 10px;
    // font-size: 20px;
    font-family: $fontAmatic;
    font-size: 26px;
    letter-spacing: 1px;
  }
  .info-items {
    width: 100%;
    margin-top: 20px;
    display: grid;
    grid-gap: 10px;
    grid-template-columns: 1fr 1fr;

    @media (max-width: 1023px) {
      grid-template-columns: 1fr;
    }
    .store-hours,
    .store-info {
      display: flex;
      flex-direction: column;
      h3 {
        text-align: center;
        color: $black;
        text-transform: uppercase;
        font-family: $fontLeckerli;
        font-size: 30px;
      }
      .store-hours-items,
      .store-info-items {
        width: 100%;
        .store-hours-item,
        .store-info-item {
          width: 100%;
          display: flex;
          margin-top: 10px;
          .item {
            flex-basis: 50%;
            text-transform: uppercase;
            font-weight: bold;
            font-size: 18px;

            @media (max-width: 576px) {
              font-size: 14px;
            }
            @media (max-width: 359px) {
              font-size: 12px;
            }
          }
          .item-1 {
            text-align: right;
            padding-right: 20px;
            color: $black;

            @media (max-width: 576px) {
              padding-right: 10px;
            }
          }
          .item-2 {
            text-align: left;
            padding-left: 20px;
            @media (max-width: 576px) {
              padding-left: 10px;
            }
          }
          .green {
            color: $green;
          }
          .darkRed {
            color: $darkRed;
          }
        }
      }
    }

    .store-info-items {
      width: 80% !important;
      margin: 0 auto;

      .store-info-item {
        .item-1 {
          flex-basis: 20% !important;
        }
        .item-2 {
          flex-basis: 80% !important;
        }
        margin-top: 20px !important;
        .fa-map-marked-alt,
        .fa-phone {
          color: $orange;
          font-size: 30px;
        }
        // .info-item {
        //   font-size: 30px;
        // }
        p {
          // font-family: $fontRobotoSlab;
          font-family: $fontOpenSans;
          font-size: 24px;
          color: $black;
          line-height: 1.2;
          display: flex;
          align-items: flex-start;
        }
      }
    }

    .store-logo {
      display: flex;
      justify-content: center;
      align-items: center;
      img {
        width: 100%;
        max-width: 240px;
      }
    }

    @media (max-width: 1023px) {
      .store-hours {
        order: 1;
      }
      .store-info {
        order: 2;
      }
      // .store-logo {
      //   order: 3;
      // }
    }
    // .fa-map-marked-alt,
    // .fa-phone {
    //   font-size: 20px;
    // }
  }
}
